<template>
  <div class="role">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-row>
        <el-col :span="6">
          <el-button type="primary" @click="addDia = true">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 表格 -->
      <el-table :data="roleList" style="width: 100%" border>
        <el-table-column type="expand">
          <template slot-scope="scop">
            <!-- {{scop.row}} -->
            <!-- 一级 -->
            <el-row
              type="flex"
              align="middle"
              v-for="(item,index) in scop.row.children"
              :key="item.id"
              :zz="index"
              :style="{'border-bottom':'1px solid #eee', 'border-top': index===0?'1px solid #eee':''}"
            >
              <el-col :span="5">
                <el-tag
                  key="tag"
                  closable
                  @close="deleteRolesRights(scop.row,item.id)"
                >{{item.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 二级 -->
              <el-col :span="19">
                <el-row
                  type="flex"
                  align="middle"
                  v-for="(item2,index2) in item.children"
                  :key="item2.id"
                  :zz="index2"
                >
                  <el-col :span="5">
                    <el-tag
                      key="tag"
                      type="success"
                      closable
                      @close="deleteRolesRights(scop.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="19">
                    <el-tag
                      type="warning"
                      :key="index3"
                      closable
                      v-for="(item3,index3) in item2.children"
                      @close="deleteRolesRights(scop.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scop">
            <!-- {{scop.row}} -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="getRoleInfo(scop.row.id)"
            >编辑</el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteRoles(scop.row.id)"
            >删除</el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="getRightAll(scop.row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 添加角色弹出框 -->
      <el-dialog title="添加角色" :visible.sync="addDia" width="30%" @close="biaodan('addRef')">
        <el-form
          :model="addForm"
          :rules="addRul"
          ref="addRef"
          label-position="left"
          label-width="30%"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="addForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDia = false">取 消</el-button>
          <el-button type="primary" @click="addRoles">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 修改用户 -->
      <el-dialog title="修改角色" :visible.sync="alterDia" width="30%" @close="biaodan('alterRef')">
        <el-form :model="alterRoleFrom" :rules="alterRul" ref="alterRef">
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="alterRoleFrom.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="alterRoleFrom.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="alterDia = false">取 消</el-button>
          <el-button type="primary" @click="alterRoles">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 分配权限 -->
      <el-dialog title="分配权限" :visible.sync="assRightDia" width="30%">
        <el-tree
          :data="rightList"
          show-checkbox
          node-key="id"
          :default-expand-all="true"
          :default-checked-keys="defaultsCheckedArr"
          :props="defaultProps"
          ref="tree"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="assRightDia = false">取 消</el-button>
          <el-button type="primary" @click="upDataRight">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 角色列表数据
      roleList: [],
      // 添加弹出框
      addDia: false,
      // 添加数据
      addForm: {
        roleName: "",
        roleDesc: ""
      },
      // 添加规则
      addRul: {
        roleName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        roleDesc: [{ required: true, message: "请输入用户名", trigger: "blur" }]
      },
      // 修改弹框
      alterDia: false,
      // 修改数据
      alterRoleFrom: {
        roleId: "",
        roleName: "",
        roleDesc: ""
      },
      // 修改规则
      alterRul: {
        roleName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        roleDesc: [{ required: true, message: "请输入用户名", trigger: "blur" }]
      },
      // 选择权限弹出框
      assRightDia: false,
      // tree配置项
      defaultProps: {
        label: "authName",
        children: "children"
      },
      // tree数据
      rightList: [],
      // 选择中的数据
      defaultsCheckedArr: [],
      roleId: ""
    };
  },
  methods: {
    async getRoleList() {
      let { data: res } = await this.$http.get("roles");
      if (res.meta.status != 200) {
        return this.$message.error("获取用户失败");
      }
      this.roleList = res.data;
    },
    // 添加用户
    addRoles() {
      this.$refs.addRef.validate(async valid => {
        if (!valid) return;
        let { data: res } = await this.$http.post("roles", this.addForm);
        if (res.meta.status != 201) {
          return this.$message.error("角色添加失败");
        }
        this.$message.success("用户添加成功");
        this.getRoleList();
        this.addDia = false;
      });
    },
    // 修改用户信息--
    async getRoleInfo(id) {
      this.alterDia = true;
      let { data: res } = await this.$http.get("roles/" + id);
      if (res.meta.status != 200) {
        return this.$message.error("获取角色信息失败");
      }
      this.alterRoleFrom = res.data;
    },
    // 提交修改
    async alterRoles() {
      let { data: res } = await this.$http.put(
        "roles/" + this.alterRoleFrom.roleId,
        this.alterRoleFrom
      );
      if (res.meta.status != 200) {
        return this.$message.error("修改失败");
      }
      this.$message.success("修改成功");
      this.alterDia = false;
      this.getRoleList();
    },
    // 删除角色
    async deleteRoles(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          await this.$http.delete("roles/" + id).then(() => {
            this.$message.success("删除成功");
            this.getRoleList();
          });
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 删除权限列表
    async deleteRolesRights(row, id) {
      let { data: res } = await this.$http.delete(
        `roles/${row.id}/rights/${id}`
      );
      if (res.meta.status != 200) {
        return this.$message.error("取消权限失败");
      }
      this.$message.success("取消权限成功");
      row.children = res.data;
    },
    // 点击获取权限列表
    async getRightAll(row) {
      this.assRightDia = true;
      this.roleId = row.id;
      let { data: res } = await this.$http.get("rights/tree");
      if (res.meta.status != 200) {
        return this.$message.error("获取权限列表失败");
      }
      this.rightList = res.data;
      // 回显数组
      let idArr = [];
      this.getListRight(row, idArr);

      this.defaultsCheckedArr = idArr;
    },
    // 遍历所有id
    getListRight(role, idArr) {
      if (!role.children) {
        return idArr.push(role.id);
      }
      role.children.forEach(item => this.getListRight(item, idArr));
    },
    // 修改权限提交
    async upDataRight() {
      let halfCheck = this.$refs.tree.getHalfCheckedKeys();
      let fullCheck = this.$refs.tree.getCheckedKeys();
      let arr = halfCheck.concat(fullCheck);
      arr = arr.join(",");
      let {data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{
        rids:arr
      })
      if(res.meta.status != 200){
        return this.$message.error("权限修改失败")
      }
      this.$message.success("权限修改成功")
      this.assRightDia =false;
      this.getRoleList()
      
    },
    // 表单重置大法
    biaodan(row) {
      this.$refs[row].resetFields();
    }
  },
  created: function() {
    this.getRoleList();
  }
};
</script>

<style>
.el-tag {
  margin: 10px 5px;
}
</style>
